
<template>
  <div class="login">
    <div class="header">
      <span>项目管理平台</span>
    </div>
    <div class="bj">
      <img src="../assets/u70.png" width="60%">
      <div class="login_in">
        <div class="login_on">
          <h3 class="pt">平台登陆</h3>
          <template>
          <el-input placeholder="请输入内容" class="yanzheng user" v-model="user">
          <template slot="prepend">账号</template>
          </el-input>
          <template>
          <el-alert v-if="error1" title="" type="error" show-icon  class="user_error">用户名不存在</el-alert>
          </template>
          <el-input placeholder="请输入内容" class="yanzheng password" v-model="password" show-password>
          <template slot="prepend">密码</template>
          </el-input>
           <template>
          <el-alert v-if="error2" title="" type="error" show-icon  class="user_error">密码错误，请重新输入</el-alert>
          </template>
           <el-input placeholder="请输入内容" class="yanzheng password" v-model="email">
          <template slot="prepend">邮箱</template>
          </el-input>
           <template>
          <el-alert v-if="error3" title="" type="error" show-icon  class="user_error">邮箱错误，请重新输入</el-alert>
          </template>
          <el-button type="primary" class="yanzheng cmt_login" @click="submit">登录</el-button>
          </template>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
import index from '../views/index'
export default {
  component:index,
  data() {
    return {
      user:'',
      password:'',
      email: '',
      error1:false,
      error2:false,
      error3:false
    }
  },
  methods: {
    submit(){
        if(this.user!="admin"){
          this.error1=true
        }else if(this.password!=123456&&this.user=="admin"){
          this.error1=false
           this.error2=true
        }else if(this.email!="12345@qq.com"&&this.password==123456){
           this.error2=false
           this.error3=true
        }else if(this.email=="12345@qq.com"){
           this.error3=false
        }if(this.user=="admin"&&this.password==123456&&this.email=="12345@qq.com"){
          this.$router.push({
            path:'/index'
        })
        }
    
    }
  },
}
</script>

